import React from 'react';
import './layout.css';
import map from './map.png';
import marker from './marker.png';

function getMarker(markers){
 const pin = markers.map((coordinate, idx)=>{
   return (
    <div key={idx} className="map-marker" style={{
          top: coordinate[0] + 'px',
          left: coordinate[1] + 'px'
        }}>
        <img src={marker}/>
    </div>
   );
 })
 return pin;
}

export default class Layout extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      makers:[]
    }
  }

  handleImgClick = (e) => {
    console.log('click', e);
    const markers = this.state.makers;

    markers.push([e.nativeEvent.offsetY - 15, e.nativeEvent.offsetX - 15]);
    this.setState({
      makers: markers
    });
  }

  render(){
    return (
      <div className='layout'
      >
        <img src={map} onClick={(e)=>{this.handleImgClick(e);}}/>
        {getMarker(this.state.makers)}
      </div>
    );
  };
}